"use client";

import React, { useMemo, useState } from "react";
import PageBreadcrumb from "@/components/common/PageBreadCrumb";
import {
    Table,
    TableBody,
    TableCell,
    TableHeader,
    TableRow,
} from "@/components/ui/table";
import Badge from "@/components/ui/badge/Badge";
import { Modal } from "@/components/ui/modal";
import Image from "next/image";
import { Plus, Upload, FileText } from "lucide-react";

/* ---------------- Types ---------------- */
type Status = "Draft" | "Submitted" | "Approved" | "Rejected";

interface Receipt {
    name: string;
    url: string;
    type: "image" | "pdf";
}

interface Reimbursement {
    id: number;
    title: string;
    amount: number;
    date: string;
    month: string;
    status: Status;
    description: string;
    receipts: Receipt[];
}

/* ---------------- Mock Data ---------------- */
const initialData: Reimbursement[] = [
    {
        id: 1,
        title: "Client Lunch",
        amount: 120,
        date: "2025-01-12",
        month: "2025-01",
        status: "Draft",
        description: "Lunch with ABC Corp",
        receipts: [],
    },
    {
        id: 2,
        title: "Taxi Fare",
        amount: 45,
        date: "2025-01-05",
        month: "2025-01",
        status: "Submitted",
        description: "Airport drop",
        receipts: [],
    },
];

/* ---------------- Component ---------------- */
export default function ReimbursementPage() {
    const [records, setRecords] = useState(initialData);
    const [selected, setSelected] = useState<Reimbursement | null>(null);
    const [modalOpen, setModalOpen] = useState(false);

    /* Form State */
    const [title, setTitle] = useState("");
    const [amount, setAmount] = useState<number>(0);
    const [date, setDate] = useState("");
    const [description, setDescription] = useState("");
    const [receipts, setReceipts] = useState<Receipt[]>([]);

    /* Filters */
    const [statusFilter, setStatusFilter] = useState<Status | "All">("All");
    const [monthFilter, setMonthFilter] = useState("All");

    /* Pagination */
    const [page, setPage] = useState(1);
    const perPage = 5;

    /* ---------------- Derived Data ---------------- */
    const filteredData = useMemo(() => {
        return records.filter(r => {
            const statusMatch = statusFilter === "All" || r.status === statusFilter;
            const monthMatch = monthFilter === "All" || r.month === monthFilter;
            return statusMatch && monthMatch;
        });
    }, [records, statusFilter, monthFilter]);

    const totalPages = Math.ceil(filteredData.length / perPage);
    const pageData = filteredData.slice((page - 1) * perPage, page * perPage);

    /* ---------------- Actions ---------------- */
    const openNew = () => {
        setSelected(null);
        setTitle("");
        setAmount(0);
        setDate("");
        setDescription("");
        setReceipts([]);
        setModalOpen(true);
    };

    const openDetail = (r: Reimbursement) => {
        setSelected(r);
        setTitle(r.title);
        setAmount(r.amount);
        setDate(r.date);
        setDescription(r.description);
        setReceipts(r.receipts);
        setModalOpen(true);
    };

    const saveDraft = () => {
        if (selected) {
            setRecords(records.map(r =>
                r.id === selected.id
                    ? { ...r, title, amount, date, description, receipts }
                    : r
            ));
        } else {
            setRecords([
                {
                    id: Date.now(),
                    title,
                    amount,
                    date,
                    month: date.slice(0, 7),
                    description,
                    receipts,
                    status: "Draft",
                },
                ...records,
            ]);
        }
        setModalOpen(false);
    };

    const submitRecord = () => {
        if (!selected) return;
        setRecords(records.map(r =>
            r.id === selected.id ? { ...r, status: "Submitted" } : r
        ));
        setModalOpen(false);
    };

    /* ---------------- Upload ---------------- */
    const uploadFiles = (files: FileList | null) => {
        if (!files) return;
        const uploaded: Receipt[] = Array.from(files).map(f => ({
            name: f.name,
            url: URL.createObjectURL(f),
            type: f.type.includes("pdf") ? "pdf" : "image",
        }));
        setReceipts(prev => [...prev, ...uploaded]);
    };

    /* ---------------- UI ---------------- */
    return (
        <div className="p-6">
            <PageBreadcrumb pageTitle="Reimbursements" />

            {/* Header */}
            <div className="flex flex-wrap gap-3 justify-between mb-4">
                <div className="flex gap-3">
                    <select
                        className="border rounded px-3 py-2"
                        value={statusFilter}
                        onChange={e => setStatusFilter(e.target.value as any)}
                    >
                        <option value="All">All Status</option>
                        <option value="Draft">Draft</option>
                        <option value="Submitted">Submitted</option>
                        <option value="Approved">Approved</option>
                        <option value="Rejected">Rejected</option>
                    </select>

                    <select
                        className="border rounded px-3 py-2"
                        value={monthFilter}
                        onChange={e => setMonthFilter(e.target.value)}
                    >
                        <option value="All">All Months</option>
                        <option value="2025-01">Jan 2025</option>
                    </select>
                </div>

                <button
                    onClick={openNew}
                    className="flex items-center gap-2 bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
                >
                    <Plus size={16} /> New Reimbursement
                </button>
            </div>

            {/* -------- Nice Table -------- */}
            <div className="overflow-hidden rounded-xl border border-gray-200 bg-white">
                <div className="max-w-full overflow-x-auto">
                    <div className="min-w-[1000px]">
                        <Table>
                            <TableHeader className="border-b">
                                <TableRow>
                                    <TableCell isHeader className="px-5 py-3 text-left text-gray-500">
                                        Title
                                    </TableCell>
                                    <TableCell isHeader className="px-5 py-3 text-left text-gray-500">
                                        Amount
                                    </TableCell>
                                    <TableCell isHeader className="px-5 py-3 text-left text-gray-500">
                                        Date
                                    </TableCell>
                                    <TableCell isHeader className="px-5 py-3 text-left text-gray-500">
                                        Status
                                    </TableCell>
                                </TableRow>
                            </TableHeader>

                            <TableBody className="divide-y">
                                {pageData.map(r => (
                                    <TableRow key={r.id}>
                                        <TableCell className="px-5 py-4">
                                            <button
                                                onClick={() => openDetail(r)}
                                                className="font-medium text-blue-600 hover:underline"
                                            >
                                                {r.title}
                                            </button>
                                            <div className="text-xs text-gray-500">
                                                {r.description}
                                            </div>
                                        </TableCell>

                                        <TableCell className="px-5 py-4">
                                            ${r.amount}
                                        </TableCell>

                                        <TableCell className="px-5 py-4">
                                            {r.date}
                                        </TableCell>

                                        <TableCell className="px-5 py-4">
                                            <Badge
                                                size="sm"
                                                color={
                                                    r.status === "Approved"
                                                        ? "success"
                                                        : r.status === "Submitted"
                                                            ? "warning"
                                                            : r.status === "Rejected"
                                                                ? "error"
                                                                : "secondary"
                                                }
                                            >
                                                {r.status}
                                            </Badge>
                                        </TableCell>
                                    </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </div>
                </div>
            </div>

            {/* Pagination */}
            {totalPages > 1 && (
                <div className="mt-4 flex justify-center gap-2">
                    {[...Array(totalPages)].map((_, i) => (
                        <button
                            key={i}
                            onClick={() => setPage(i + 1)}
                            className={`px-3 py-1 rounded border ${page === i + 1 ? "bg-blue-600 text-white" : ""
                                }`}
                        >
                            {i + 1}
                        </button>
                    ))}
                </div>
            )}

            {/* -------- Modal -------- */}
            <Modal isOpen={modalOpen} onClose={() => setModalOpen(false)} className="max-w-xl p-8">
                <h2 className="text-xl font-semibold mb-4">
                    {selected ? "Reimbursement Details" : "New Reimbursement"}
                </h2>

                <div className="space-y-4">
                    <input className="w-full border px-3 py-2 rounded" placeholder="Title" value={title} onChange={e => setTitle(e.target.value)} />
                    <input type="number" className="w-full border px-3 py-2 rounded" placeholder="Amount" value={amount} onChange={e => setAmount(+e.target.value)} />
                    <input type="date" className="w-full border px-3 py-2 rounded" value={date} onChange={e => setDate(e.target.value)} />
                    <textarea className="w-full border px-3 py-2 rounded" placeholder="Description" value={description} onChange={e => setDescription(e.target.value)} />

                    {/* Upload */}
                    <label className="flex items-center gap-2 cursor-pointer text-blue-600">
                        <Upload size={16} />
                        Upload Receipts
                        <input type="file" multiple hidden onChange={e => uploadFiles(e.target.files)} />
                    </label>

                    {/* Preview */}
                    <div className="grid grid-cols-3 gap-2">
                        {receipts.map((r, i) => (
                            <div key={i} className="border rounded p-2 text-center">
                                {r.type === "image" ? (
                                    <img src={r.url} className="h-20 mx-auto object-cover" />
                                ) : (
                                    <FileText className="mx-auto" />
                                )}
                                <p className="text-xs truncate">{r.name}</p>
                            </div>
                        ))}
                    </div>

                    <div className="flex justify-end gap-2 pt-4">
                        <button onClick={saveDraft} className="px-4 py-2 bg-gray-200 rounded">
                            Save Draft
                        </button>
                        {selected?.status === "Draft" && (
                            <button onClick={submitRecord} className="px-4 py-2 bg-blue-600 text-white rounded">
                                Submit
                            </button>
                        )}
                    </div>
                </div>
            </Modal>
        </div>
    );
}
